import { NextResponse } from "next/server"
import { cookies } from "next/headers"

export async function GET() {
  try {
    // Check if we have an auth cookie
    const cookieStore = await cookies()
    const authCookie = cookieStore.get("auth")

    if (!authCookie) {
      return NextResponse.json({ user: null }, { status: 200 })
    }

    // Call the backend API using fetch
    const backendRes = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3001'}/auth/session`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${authCookie.value}`,
      },
    })

    const responseData = await backendRes.json()

    if (!backendRes.ok) {
      return NextResponse.json({ user: null }, { status: 200 })
    }

    return NextResponse.json({ user: responseData.user }, { status: 200 })
  } catch (error: any) {
    return NextResponse.json({ user: null }, { status: 200 })
  }
} 